/* use inline nav*/
.site-archive-header .site-header-background {
    margin-top: 0;
}

.site-archive-header .site-nav-logo {
    display: none;
}

.no-image .author-social-link a {
    color: inherit;
}

.author-profile-image {
    background: #c5d2d9;
}

.author-profile-image path {
    fill: #738a94 !important;
}